@import url('https://fonts.googleapis.com/css2?family=Cairo&display=swap');

html, body {
    margin: 0;
    padding: 0;
    font-family: 'Cairo', sans-serif;
    direction: rtl;
    background-color: #f8f9fa;
    overflow-x: hidden;
    height: 100%;
}

.login-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.logo-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
}

.login-logo-image {
    height: 80px; /* يمكنك تعديل الحجم حسب الحاجة */
    margin-bottom: 10px;
}

.login-title {
    font-size: 24px; /* تعديل حجم الخط */
    color: #001529;
}

.my-4 {
    margin-top: 2.5rem !important;
    margin-bottom: 1.5rem !important;
}
